import {Component, h} from 'preact';
import * as modal from '../../utils/modal';
import style from './style.scss';
import {bind} from 'decko';

const Utils = window.Utils;

/**
 * 选择投保险种
 */
class Modal extends Component {
    constructor(props) {
        super(props);
        this.state = {
            modalId: ''
        };
    }

    static defaultProps = {
        value: false,
        body: '',
        confirmText: '',
        trackName: null,
        cancelText: '',
        onConfirm() {
        },
        onCancel() {
        }
    };

    componentDidMount() {
        console.log(this.props);
        this.setState({
            modalId: modal.init({
                className: style['base-modal'],
                body: this.props.body,
                confirmText: this.props.confirmText,
                cancelText: this.props.cancelText,
                confirm: this.onConfirm,
                cancel: this.onCancel
            })
        });
    }

    @bind
    onConfirm(value) {
        if (this.props.trackName !== null && this.props.trackName !== undefined) {
            Utils.sendTrack('click', this.props.trackName + 'ModalConfirm');
        }
        this.props.onConfirm(value);
    }

    @bind
    onCancel(value) {
        if (this.props.trackName !== null && this.props.trackName !== undefined) {
            Utils.sendTrack('click', this.props.trackName + 'ModalCancel');
        }
        this.props.onCancel(value);
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.value) {
            modal.show(this.state.modalId);
            if (this.props.trackName !== null && this.props.trackName !== undefined && nextProps.value !== this.props.value) {
                Utils.sendTrack('click', this.props.trackName + 'ModalShow', {
                    type: 'hide'
                });
            }
        } else {
            modal.hide(this.state.modalId);
            if (this.props.trackName !== null && this.props.trackName !== undefined && nextProps.value !== this.props.value) {
                Utils.sendTrack('click', this.props.trackName + 'ModalCancel', {
                    type: 'show'
                });
            }
        }
    }

    componentWillUnmount() {
        modal.destroy(this.state.modalId);
    }
}

export default Modal;


